import React, { useState } from 'react';
import './categoryProducts.scss';
import { useParams } from 'react-router-dom';
import ar from '../../assets/languages/ar.json';
import { ProductCard } from '../../components/productCard/productCard';
import { getCategoryProducts } from '../../utils/getCategoryProducts';
import { ProductNav } from '../../components/productsNav/productNav';
import { ComponentHeader } from '../../components/componentHeader/componentHeader';
import { LoadMoreButton } from '../../components/loadMoreButton/loadMoreButton';

export const CategoryProducts: React.FC = () => {
  const { categoryId } = useParams();
  const productsStaticData = getCategoryProducts(categoryId);
  const [productsToShow, setProductsToShow] = useState(8); // initial values is 8

  const loadMoreProducts = () => {
    const newProductsToShow = productsToShow + 8;
    setProductsToShow(newProductsToShow);
  };
  return (
    <div>
      {categoryId && productsStaticData ? (
        <div className="categoryProducts">
          {productsStaticData?.title && <ProductNav description={productsStaticData?.title} />}
          {productsStaticData?.title && <ComponentHeader title={productsStaticData.title} />}
          <div className="categoryProducts__body">
            {productsStaticData.products.slice(0, productsToShow).map((product, index) => (
              <div className="categoryProducts__item" key={index}>
                <ProductCard key={index} category={categoryId} product={product} />
              </div>
            ))}
          </div>
          {productsToShow < productsStaticData.products.length && (
            <LoadMoreButton onClick={loadMoreProducts} title={ar.loadMore} />
          )}
        </div>
      ) : (
        <div className="productNotFound"> {ar.categoryNotFound}</div>
      )}
    </div>
  );
};
