import React, { useEffect } from 'react';
import 'react-slideshow-image/dist/styles.css';
import { Header } from './components/header/header';
import { Footer } from './components/footer/footer';
import { Routes, Route, useLocation, HashRouter } from 'react-router-dom';
import { HomePage } from './pages/homePage/homePage';
import { ProductDetails } from './pages/productDetails/productDetails';
import { CategoryProducts } from './pages/categoryProducts/categoryProducts';
import { ShopByBrand } from './pages/shopByBrand/shopByBrand';
import { ScrollUpButton } from './components/scrollUpButton/scrollUpButton';

const ScrollToTop = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
};

export const App: React.FC = () => {
  return (
    <div>
      <HashRouter>
        <Header />
        <ScrollToTop />
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="*" element={<HomePage />} />
          <Route path="/products/:categoryId" element={<CategoryProducts />} />
          <Route path="/products/:categoryId/:productId" element={<ProductDetails />} />
          <Route path="/shopByBrand/:brandId" element={<ShopByBrand />} />
        </Routes>
        <Footer />
        <ScrollUpButton />
      </HashRouter>
    </div>
  );
};
