import React, { useEffect, useState } from 'react';
import './shopByBrand.scss';
import { Link, useParams } from 'react-router-dom';
import ar from '../../assets/languages/ar.json';
import { IProductCard, ProductCard } from '../../components/productCard/productCard';
import { ProductNav } from '../../components/productsNav/productNav';
import shopByBrandImage from '../../assets/images/shopByBrand.jpg';
import { getProductsByBrand } from '../../utils/getProductsByBrand';
import { getBrands } from '../../utils/getBrands';
import { Slide } from 'react-slideshow-image';
import { LoadMoreButton } from '../../components/loadMoreButton/loadMoreButton';

export const ShopByBrand: React.FC = () => {
  const { brandId } = useParams();
  const [productsToShow, setProductsToShow] = useState(8); // initial values is 8
  const [brands, setBrands] = useState<string[]>([]);
  const [currentBrandProducts, setCurrentBrandProducts] = useState<IProductCard[] | undefined>(undefined);

  const loadMoreProducts = () => {
    const newProductsToShow = productsToShow + 8;
    setProductsToShow(newProductsToShow);
  };

  useEffect(() => {
    if (brandId) {
      const res = getProductsByBrand(brandId);
      setCurrentBrandProducts(res);
    }
  }, [brandId]);

  useEffect(() => {
    setBrands(getBrands());
  }, []);
  return (
    <div className="shopByBrand">
      <ProductNav description={ar.shopByBrand} />
      <div className="shopByBrand__headerImage">
        <img src={shopByBrandImage} alt="shopByBrandImage" />
      </div>
      <div className="shopByBrand__buttons">
        <Slide
          slidesToScroll={1}
          slidesToShow={1}
          autoplay={false}
          responsive={[
            {
              breakpoint: 450,
              settings: {
                slidesToShow: brands.length > 7 ? 7 : brands.length,
                slidesToScroll: brands.length > 7 ? 7 : brands.length,
              },
            },
            {
              breakpoint: 1, // this breakpoint will starts from 1 px
              settings: {
                slidesToShow: brands.length > 3 ? 3 : brands.length,
                slidesToScroll: brands.length > 3 ? 3 : brands.length,
              },
            },
          ]}
        >
          {brands.map((brand, index) => (
            <Link className="noDecoration" to={`/shopByBrand/${brand}`}>
              <div
                key={index}
                className={`shopByBrand__button ${brand === brandId ? 'selectedBrand' : ''}`}
                // onClick={() => setSelectedBrand(brand)}
              >
                {brand}
              </div>
            </Link>
          ))}
        </Slide>
      </div>
      {brandId && currentBrandProducts && currentBrandProducts.length > 0 ? (
        <>
          <div className="shopByBrand__body">
            {currentBrandProducts.slice(0, productsToShow).map((product, index) => (
              <div className="shopByBrand__item" key={index}>
                <ProductCard key={index} category={product.category} product={product.product} />
              </div>
            ))}
          </div>
          {productsToShow < currentBrandProducts.length && (
            <LoadMoreButton onClick={loadMoreProducts} title={ar.loadMore} />
          )}
        </>
      ) : (
        <div className="productNotFound"> {ar.productNotFound}</div>
      )}
    </div>
  );
};
