import React from 'react';
import './loadMoreButton.scss';
import { Link } from 'react-router-dom';

interface ILoadMoreButton {
  title: string;
  link?: string;
  onClick?(): void;
}
export const LoadMoreButton: React.FC<ILoadMoreButton> = (loadMoreButton) => {
  return (
    <div className="loadMoreButton">
      {loadMoreButton.link ? (
        <Link className="noDecoration" to={loadMoreButton.link}>
          <div>{loadMoreButton.title}</div>
        </Link>
      ) : (
        <div onClick={loadMoreButton.onClick}>{loadMoreButton.title}</div>
      )}
    </div>
  );
};
