import { IProduct } from '../types';

export const getDefaultImageByColor = (category: string, product: IProduct, color: string) => {
  const imagesArr = product.media[color] || [];
  const defaultImage = imagesArr[0] || 'couldNotFindTheDefaultProductCardImage';

  return `media/${category}/${product.id}/${color}/${defaultImage}`;
};
