import React from 'react';
import { MainSlider } from '../../components/mainSlider/mainSlider';
import { ProductsSlider } from '../../components/productsSlider/productsSlider';
import { IProductsStaticData } from '../../types';
import { ShopByBrandSlider } from '../../components/shopByBrandSlider/shopByBrandSlider';
import { FollowUsOn } from '../../components/followUsOn/followUsOn';

export const HomePage: React.FC = () => {
  const mainSliderData = require('../../assets/staticData/mainSlider.json');
  const categories: string[] = require('../../assets/staticData/categories.json').categories;

  return (
    <div>
      <MainSlider category={mainSliderData.category} products={mainSliderData.products} />
      {/* list our categories products as sliders */}
      {categories.map((category, index) => {
        try {
          const categoryProducts: IProductsStaticData = require(`../../assets/staticData/${category}.json`);
          return (
            <ProductsSlider
              key={index}
              title={categoryProducts.title}
              category={categoryProducts.category}
              products={categoryProducts.products}
            />
          );
        } catch (error) {
          // in case couldn't find ${category}.json
          return <div key={index}></div>;
        }
      })}
      <FollowUsOn />
      <ShopByBrandSlider />
    </div>
  );
};
