import React from 'react';
import './mainSlider.scss';
import { Zoom } from 'react-slideshow-image';
import ar from '../../assets/languages/ar.json';
import { Link } from 'react-router-dom';

interface IProduct {
  title: string;
  image: string;
  link: string;
}

interface IMainSliderData {
  category: string;
  products: IProduct[];
}

export const MainSlider: React.FC<IMainSliderData> = (props: IMainSliderData) => {
  return (
    <div className="mainSlider">
      <Zoom indicators scale={1.5} autoplay={true}>
        {props.products.map((product, index) => {
          return (
            <div key={index}>
              <div className="mainSlider__img">
                <img
                  alt="mainSliderImg"
                  src={require(`../../assets/staticData/media/${props.category}/${product.image}`)}
                ></img>
              </div>
              <div className="mainSlider__title">
                <p>{product.title}</p>
                <Link className="noDecoration" to={product.link}>
                  <button>{ar.shoppingNow}</button>
                </Link>
              </div>
            </div>
          );
        })}
      </Zoom>
    </div>
  );
};
