import React from 'react';
import './productCard.scss';
import ar from '../../assets/languages/ar.json';
import { Link } from 'react-router-dom';
import { IProduct } from '../../types';
import { getDefaultImage } from '../../utils/getDefaultImage';
import { getDiscountPercentage } from '../../utils/getDiscountPercentage';

export interface IProductCard {
  category: string;
  product: IProduct;
}
export const ProductCard: React.FC<IProductCard> = (props: IProductCard) => {
  return (
    <div className="productCard">
      <Link className="noDecoration" to={`/products/${props.category}/${props.product.id}`}>
        <div className="productCard__item">
          <div className="productCard__item__mainImage">
            <div className="productCard__badges">
              {props.product.isNew && (
                <div className="circularBadge">
                  <span>{ar.new}</span>
                </div>
              )}
              {props.product.oldPrice && (
                <div className="circularBadge discountColor">
                  <span>{getDiscountPercentage(props.product.price, props.product.oldPrice)}%-</span>
                </div>
              )}
            </div>
            <img
              src={require(`../../assets/staticData/${getDefaultImage(props.category, props.product)}`)}
              alt="cardImage"
            />
          </div>

          <h3 className="productCard__item__discretion">{props.product.description}</h3>
          <div className="productCard__item__price">
            {props.product.oldPrice && (
              <del>
                {props.product.oldPrice} {ar.sar}
              </del>
            )}
            <span className={`${props.product.oldPrice ? `redColor` : ''}`}>
              {props.product.price} {ar.sar}
            </span>
          </div>
          <div className="productCard__item__price">{props.product.id}</div>
        </div>
      </Link>
    </div>
  );
};
