import React, { useCallback, useEffect, useState } from 'react';
import './productDetails.scss';
import { useParams } from 'react-router-dom';
import ar from '../../assets/languages/ar.json';
import ImageGallery from 'react-image-gallery';
import { ProductCard } from '../../components/productCard/productCard';
import { getDefaultImageByColor } from '../../utils/getDefaultImageByColor';
import { getCategoryProducts } from '../../utils/getCategoryProducts';
import { getDefaultOurPhotography } from '../../utils/getDefaultOurPhotography';
import { ProductNav } from '../../components/productsNav/productNav';
import { getDiscountPercentage } from '../../utils/getDiscountPercentage';
import { ComponentHeader } from '../../components/componentHeader/componentHeader';

export const ProductDetails: React.FC = () => {
  const { categoryId, productId } = useParams();
  const productsStaticData = getCategoryProducts(categoryId);
  const product = productsStaticData?.products.find((product) => product.id === productId);
  const [currentCalor, setCurrentCalor] = useState('');

  const getYouMayAlsoLikeProducts = () => {
    const youMayAlsoLikeProducts = productsStaticData?.products.filter((p) => p.id !== product?.id);
    return youMayAlsoLikeProducts?.slice(0, 4) || [];
  };

  const getProductMedia = useCallback(() => {
    if (currentCalor === 'ourPhotography') {
      const productMedia =
        (product &&
          productsStaticData?.category &&
          product.ourPhotography.length > 0 &&
          product.ourPhotography.map((image) => {
            if (image.includes('.mp4')) {
              return {
                originalClass: 'productDetails__originalClass',
                thumbnailClass: 'productDetails__thumbnailClass',
                original: require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/ourPhotography/${image}`),
                // thumbnail: require(`../../assets/staticData/media/${productsStaticData?.category}/${product.id}/ourPhotography/${image}`),
                renderItem: () => (
                  <video
                    className="image-gallery-image"
                    controlsList={'nodownload'}
                    playsInline
                    muted
                    loop
                    controls
                    autoPlay
                    src={require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/ourPhotography/${image}`)}
                    // type="video/mp4"
                  />
                ),
              };
            }
            return {
              originalClass: 'productDetails__originalClass',
              thumbnailClass: 'productDetails__thumbnailClass',
              original: require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/ourPhotography/${image}`),
              thumbnail: require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/ourPhotography/${image}`),
            };
          })) ||
        [];
      return productMedia;
    } else {
      const productMedia =
        (product &&
          productsStaticData?.category &&
          currentCalor &&
          product.media[currentCalor] &&
          product.media[currentCalor]?.map((image) => {
            return {
              originalClass: 'productDetails__originalClass',
              thumbnailClass: 'productDetails__thumbnailClass',
              original: require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/${currentCalor}/${image}`),
              thumbnail: require(`../../assets/staticData/media/${productsStaticData.category}/${product.id}/${currentCalor}/${image}`),
            };
          })) ||
        [];
      return productMedia;
    }
  }, [currentCalor, product, productsStaticData?.category]);

  const [currentProductMedia, setCurrentProductMedia] = useState(getProductMedia());

  useEffect(() => {
    product && setCurrentCalor(Object.keys(product.media)[0]);
    return () => {};
  }, [product]);

  useEffect(() => {
    setCurrentProductMedia(getProductMedia());
    return () => {};
  }, [product, currentCalor, getProductMedia]);

  return (
    <div>
      {product ? (
        <div className="productDetails">
          {productsStaticData?.title && (
            <ProductNav title={productsStaticData?.title} categoryId={categoryId} description={product.description} />
          )}
          <div className="productDetails__cols">
            <div className="productDetails__col">
              <div className="productDetails__badges">
                {product.isNew && (
                  <div className="circularBadge">
                    <span>{ar.new}</span>
                  </div>
                )}
                {product.oldPrice && (
                  <div className="circularBadge discountColor">
                    <span>{getDiscountPercentage(product.price, product.oldPrice)}%-</span>
                  </div>
                )}
              </div>
              {currentCalor && (product.media[currentCalor] || product.ourPhotography.length > 0) && (
                <ImageGallery
                  showNav={true}
                  showIndex={true}
                  showBullets={true}
                  showPlayButton={false}
                  disableThumbnailScroll={false}
                  items={currentProductMedia}
                />
              )}
            </div>
            <div className="productDetails__col">
              <h1>{product.description}</h1>
              <h2>{ar.price}:</h2>
              <div className="productDetails__price">
                {product.oldPrice && (
                  <del>
                    {product.oldPrice} {ar.sar}
                  </del>
                )}
                <span className={`${product.oldPrice ? `redColor` : ''}`}>
                  {product.price} {ar.sar}
                </span>
              </div>
              {product.oldPrice && (
                <div className="productDetails__badgePrice">
                  {ar.economy} {parseInt(product.oldPrice) - parseInt(product.price)} {ar.sar}
                </div>
              )}
              {/* show colors buttons if we have more than one color or if there ourPhotography */}
              {categoryId && (Object.keys(product.media).length > 1 || product.ourPhotography.length > 0) && (
                <div className="productDetails__color">
                  <div>{ar.colors}:</div>
                  {Object.keys(product.media).map((color, index) => {
                    return (
                      <img
                        className={`${currentCalor === color ? 'productDetails__selectedColor' : ''}`}
                        key={index}
                        src={require(`../../assets/staticData/${getDefaultImageByColor(categoryId, product, color)}`)}
                        alt="colorImage"
                        onClick={() => setCurrentCalor(color)}
                      />
                    );
                  })}
                </div>
              )}
              {categoryId && product.ourPhotography.length > 1 && (
                <div className="productDetails__color">
                  <div>{ar.ourPhotography}:</div>
                  <img
                    className={`${currentCalor === 'ourPhotography' ? 'productDetails__selectedColor' : ''}`}
                    src={require(`../../assets/staticData/media/${categoryId}/${
                      product.id
                    }/ourPhotography/${getDefaultOurPhotography(product)}`)}
                    alt={`${getDefaultOurPhotography(product)}`}
                    onClick={() => setCurrentCalor('ourPhotography')}
                  />
                </div>
              )}
              {product.size && (
                <div className="productDetails__size">
                  {ar.size}: {product.size}
                </div>
              )}
              {product.additionalInfo.length > 1 && (
                <ul className="productDetails__additionalInfo">
                  <div>-{ar.additionalInfo}-</div>
                  {product.additionalInfo.map((info, index) => (
                    <li key={index}>{info}</li>
                  ))}
                </ul>
              )}
            </div>
          </div>
          <>
            <ComponentHeader title={ar.youMayAlsoLike} />
            <div className="youMayAlsoLike__body">
              {categoryId &&
                getYouMayAlsoLikeProducts()?.map((product, index) => (
                  <div className="youMayAlsoLike__item" key={index}>
                    <ProductCard category={categoryId} product={product} />
                  </div>
                ))}
            </div>
          </>
        </div>
      ) : (
        <div className="productNotFound"> {ar.productNotFound}</div>
      )}
    </div>
  );
};
