import React, { useEffect, useState } from 'react';
import './shopByBrandSlider.scss';
import { Slide } from 'react-slideshow-image';
import { getBrands } from '../../utils/getBrands';
import { IProductCard, ProductCard } from '../productCard/productCard';
import { getProductsByBrand } from '../../utils/getProductsByBrand';
import { ComponentHeader } from '../componentHeader/componentHeader';
import ar from '../../assets/languages/ar.json';
import { LoadMoreButton } from '../loadMoreButton/loadMoreButton';

export const ShopByBrandSlider: React.FC = () => {
  const [brands, setBrands] = useState<string[]>([]);
  const [selectedBrand, setSelectedBrand] = useState<string>('');
  const [currentBrandProducts, setCurrentBrandProducts] = useState<IProductCard[] | undefined>(undefined);

  useEffect(() => {
    const res = getProductsByBrand(selectedBrand);
    setCurrentBrandProducts(res);
  }, [selectedBrand]);

  useEffect(() => {
    setBrands(getBrands());
    setSelectedBrand(getBrands()[0]);
  }, []);

  return (
    <div className="shopByBrandSlider">
      <ComponentHeader title={ar.shopByBrand} />
      <div className="shopByBrandSlider__buttons">
        <Slide
          slidesToScroll={1}
          slidesToShow={1}
          autoplay={false}
          responsive={[
            {
              breakpoint: 450,
              settings: {
                slidesToShow: brands.length > 7 ? 7 : brands.length,
                slidesToScroll: brands.length > 7 ? 7 : brands.length,
              },
            },
            {
              breakpoint: 1, // this breakpoint will starts from 1 px
              settings: {
                slidesToShow: brands.length > 3 ? 3 : brands.length,
                slidesToScroll: brands.length > 3 ? 3 : brands.length,
              },
            },
          ]}
        >
          {brands.map((brand, index) => (
            <div
              key={index}
              className={`shopByBrandSlider__button ${brand === selectedBrand ? 'selectedBrand' : ''}`}
              onClick={() => setSelectedBrand(brand)}
            >
              {brand}
            </div>
          ))}
        </Slide>
      </div>
      <div className="shopByBrandSlider__products">
        {currentBrandProducts && currentBrandProducts.length > 0 && (
          <Slide
            slidesToScroll={1}
            slidesToShow={1}
            indicators={true}
            autoplay={true}
            responsive={[
              {
                breakpoint: 450,
                settings: {
                  slidesToShow: currentBrandProducts.length > 4 ? 4 : currentBrandProducts.length,
                  slidesToScroll: currentBrandProducts.length > 4 ? 4 : currentBrandProducts.length,
                },
              },
              {
                breakpoint: 300, // this breakpoint will starts from 300 px
                settings: {
                  slidesToShow: currentBrandProducts.length > 2 ? 2 : currentBrandProducts.length,
                  slidesToScroll: currentBrandProducts.length > 2 ? 2 : currentBrandProducts.length,
                },
              },
              {
                breakpoint: 1, // this breakpoint will starts from 1 px
                settings: {
                  slidesToShow: currentBrandProducts.length > 1 ? 1 : currentBrandProducts.length,
                  slidesToScroll: currentBrandProducts.length > 1 ? 1 : currentBrandProducts.length,
                },
              },
            ]}
          >
            {currentBrandProducts.map((currentBrandProduct, index) => (
              <ProductCard key={index} category={currentBrandProduct.category} product={currentBrandProduct.product} />
            ))}
          </Slide>
        )}
      </div>
      <LoadMoreButton link={`/shopByBrand/${selectedBrand}`} title={ar.showAll} />
    </div>
  );
};
