import { IProductCard } from '../components/productCard/productCard';
import { IProductsStaticData } from '../types';

const isProductAlreadyAdded = (localCurrentBrandProducts: IProductCard[], newBrandProduct: IProductCard) => {
  for (const localCurrentBrandProduct of localCurrentBrandProducts) {
    if (
      localCurrentBrandProduct.product.id === newBrandProduct.product.id &&
      localCurrentBrandProduct.category === newBrandProduct.category
    ) {
      return true;
    }
  }
  return false;
};

export const getProductsByBrand = (brand: string) => {
  const categories: string[] = require('../assets/staticData/categories.json').categories;
  const BrandProducts: IProductCard[] = [];

  for (const category of categories) {
    try {
      const categoryProducts: IProductsStaticData = require(`../assets/staticData/${category}.json`);
      for (const product of categoryProducts.products) {
        if (product.brand === brand) {
          if (!isProductAlreadyAdded(BrandProducts, { category, product })) BrandProducts.push({ category, product });
        }
      }
    } catch (error) {}
  }

  return BrandProducts;
};
