import { IProductsStaticData } from '../types';

export const getBrands = () => {
  const categories = require('../assets/staticData/categories.json').categories;
  const localBrands: string[] = [];
  for (const category of categories) {
    try {
      const categoryProducts: IProductsStaticData = require(`../assets/staticData/${category}.json`);
      for (const product of categoryProducts.products) {
        if (!localBrands.includes(product.brand)) {
          localBrands.push(product.brand);
        }
      }
    } catch (error) {}
  }

  return localBrands;
};
