import React from 'react';
import './productsSlider.scss';
import { Slide } from 'react-slideshow-image';
import { IProduct } from '../../types';
import { ProductCard } from '../productCard/productCard';
import { ComponentHeader } from '../componentHeader/componentHeader';

export interface IProductsSlider {
  title?: string;
  category: string;
  products: IProduct[];
}
export const ProductsSlider: React.FC<IProductsSlider> = (props: IProductsSlider) => {
  return (
    <div className="productsSlider">
      {props.title && <ComponentHeader title={props.title} />}
      <div className="productsSlider__body">
        <Slide
          slidesToScroll={1}
          slidesToShow={1}
          indicators={true}
          autoplay={true}
          responsive={[
            {
              breakpoint: 450,
              settings: {
                slidesToShow: 4,
                slidesToScroll: 4,
              },
            },
            {
              breakpoint: 300, // this breakpoint will starts from 300 px
              settings: {
                slidesToShow: 2,
                slidesToScroll: 2,
              },
            },
            {
              breakpoint: 1, // this breakpoint will starts from 1 px
              settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
              },
            },
          ]}
        >
          {props.products.map((product, index) => (
            <ProductCard key={index} category={props.category} product={product} />
          ))}
        </Slide>
      </div>
    </div>
  );
};
