import React from 'react';
import './productNav.scss';
import { Link } from 'react-router-dom';
import ar from '../../assets/languages/ar.json';

interface IProductNav {
  description: string;
  title?: string;
  categoryId?: string;
}

export const ProductNav: React.FC<IProductNav> = (props) => {
  return (
    <div className="productNav">
      <nav className="productNav__nav">
        <Link className="noDecoration" to={'/'}>
          {ar.mainPage}
        </Link>
        {props.categoryId && (
          <>
            <div className="productNav__nav__break"></div>
            <Link className="noDecoration" to={`/products/${props.categoryId}`}>
              {props.title}
            </Link>
          </>
        )}
        <div className="productNav__nav__break"></div>
        <span className="productNav__nav__description">{props.description}</span>
      </nav>
    </div>
  );
};
